.bg-bubbles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  z-index: 1;
}
.bubbles {
  position: absolute;
  list-style: none;
  display: block;
  width: 40px;
  height: 40px;
  background-color: rgba(123, 149, 235, 0.493);
  bottom: -160px;

  -webkit-animation: square 25s infinite;
  animation: square 25s infinite;
  transition-timing-function: linear;
}
.red {
  background-color: rgba(56, 241, 111, 0.493);
}
.blue {
  background-color: rgba(218, 235, 123, 0.493);
}
.bubbles:nth-child(1) {
  left: 10%;
}

.bubbles:nth-child(2) {
  left: 20%;

  width: 80px;
  height: 80px;

  -webkit-animation-delay: 2s;

          animation-delay: 2s;
  -webkit-animation-duration: 17s;
          animation-duration: 17s;
}

.bubbles:nth-child(3) {
  left: 25%;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}

.bubbles:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;

  -webkit-animation-duration: 22s;

          animation-duration: 22s;

  background-color: fade(white, 25%);
}

.bubbles:nth-child(5) {
  left: 70%;
}

.bubbles:nth-child(6) {
  left: 80%;
  width: 120px;
  height: 120px;

  -webkit-animation-delay: 3s;

          animation-delay: 3s;
  background-color: fade(white, 20%);
}

.bubbles:nth-child(7) {
  left: 32%;
  width: 160px;
  height: 160px;

  -webkit-animation-delay: 7s;

          animation-delay: 7s;
}

.bubbles:nth-child(8) {
  left: 55%;
  width: 20px;
  height: 20px;

  -webkit-animation-delay: 15s;

          animation-delay: 15s;
  -webkit-animation-duration: 40s;
          animation-duration: 40s;
}

.bubbles:nth-child(9) {
  left: 25%;
  width: 10px;
  height: 10px;

  -webkit-animation-delay: 2s;

          animation-delay: 2s;
  -webkit-animation-duration: 40s;
          animation-duration: 40s;
  background-color: fade(white, 30%);
}

.bubbles:nth-child(10) {
  left: 90%;
  width: 160px;
  height: 160px;

  -webkit-animation-delay: 11s;

          animation-delay: 11s;
}

@-webkit-keyframes square {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-700px) rotate(600deg);
            transform: translateY(-700px) rotate(600deg);
  }
}
@keyframes square {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-700px) rotate(600deg);
            transform: translateY(-700px) rotate(600deg);
  }
}

